body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*Setup*/
html,
body {
  padding: 0;
  margin: 0;
}
html,
body,
#mapid {
  height: 100%;
  width: 100%;
}

/*Legend specific*/
.legend {
  padding: 6px 8px;
  font: 14px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255, 255, 255, 0.8);
  /*box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);*/
  /*border-radius: 5px;*/
  line-height: 24px;
  color: #555;
}
.legend h4 {
  text-align: center;
  font-size: 16px;
  margin: 2px 12px 8px;
  color: #777;
}

.legend span {
  position: relative;
  bottom: 3px;
}

.legend i {
  width: 18px;
  height: 18px;
  float: left;
  margin: 0 8px 0 0;
  opacity: 0.7;
}

.legend i.icon {
  background-size: 18px;
  background-color: rgba(255, 255, 255, 1);
}

.flex {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .item {
    width: 55px;
    margin-left: 5px;
    height: 20px;

  }
  
  .line-break {
    width: 100%;
  }


  .arrow-up {
    width: 0; 
    height: 0; 
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;

    border-bottom: 18px solid #2874a6;
  }

  .arrow-down {
    width: 0; 
    height: 0; 
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    
    border-top: 18px solid #c0392b;
  }
html *{
  box-sizing:border-box;
}
.container {
  padding: 10px 15px 20px 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 970px;
}
#header{
  background-position: top right;
  background-repeat: no-repeat;
  padding-bottom: 10px;
  min-height: 50px;
}

#header a{
  text-decoration: none;
  color: #80bf43;
}

#footer {
  clear: both;
  border-top: #D8D8D8 1px solid;
  padding: 10px 0;
}

#footer a{
  color: #999;;
  font-family: Georgia, serif;
  text-decoration: none;
}

#footer a.leaflet-timedimension{
  background-position: top left;
  background-repeat: no-repeat; 
  display: block;
  float: left;
  padding-left: 55px;
  min-height: 32px;
  line-height: 32px;    
}

#footer a.github {
  padding-right: 40px;
  display: block;
  float: right;
  min-height: 32px;
  line-height: 32px;
  background-position: top right;
  background-repeat: no-repeat; 
}

a.github span{
  display: none;
}

a.github:hover span{
  display: inline;
}

.leaflet-container .legend{
  background-color:#D8D8D8;
  background-color:rgba(255,255,255,.8);border-radius:5px;
  padding: 5px;
}
.leaflet-bottom.leaflet-left {
  /*bigger margin to not overlaps with attribution*/
  margin-bottom: 10px; 
}

h1, h2{
  color: rgb(80, 75, 75);
  font-family: Georgia, serif;
  font-weight: normal;    
  margin: 5px 0px;
}

h1{
  font-style: italic;
}

h1 small{
  font-size: 12px;
  display: block;
}

h1 small a{
  text-decoration: none;
  color: #777;
}

ul.examples{
  list-style: none;
  padding: 0;
}

ul.examples li {
  float: left;
  margin-right: 5px;
  margin-bottom: 15px;
}

ul.examples li a{
  display: block;
  color: #444;
  background-color: #80bf43;
  width: 300px;
  min-height: 205px;
  text-align: center;
  text-decoration: none;
  font-size: 1.2em;
}
ul.examples li a:hover{
  text-decoration: none;
  background-color: #bae568;  
}

ul.examples li a img{
  display: block;
  margin: auto;
  margin-bottom: 15px;
}

.map{

  height: 450px;
}

.map-loading:after{
  content: "Loading...";    
  display: inline-block;
  width: 100%;    
  height: 597px;
  line-height: 597px;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  color: #FFF;
  position: absolute;
}

.map.radar{
  height: 700px;   
}

.map.sirena{
  width: 816px;
  height: 597px;   
}

.map.dark-background{
  background-color: #222;
}

#map-wrapper{
  width: 1170px;
  height: 597px;  
  background-color: #bae568;
  position: relative;
}
#map-wrapper .map{
  float:left;
}

#map-wrapper .sidecontrol{
  float: right;
  color: #444;
  width: 250px;
  padding: 20px 20px;
  margin-right: 30px;
  margin-top: 30px;
  background-color: #80bf43;
  background-color: rgba(128, 191, 67, 0.49);    
}

#map-wrapper .sidecontrol span.title a{
  display: block;
  font-size: 12px;
  color: #444;
  font-family: Georgia, serif;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  margin: auto;
  text-align: center;
}
#map-wrapper .sidecontrol span.date{
  display: block;
  font-size: 40px;
  color: #EEE;
  font-family: Georgia, serif;    
  margin: auto;
  text-align: center;
  border-bottom: 1px solid #EEE;
}
#map-wrapper .sidecontrol span.time{
  display: block;
  font-size: 80px;
  color: #EEE;
  font-family: Georgia, serif;
  font-weight: bold;
  margin: auto;
  text-align: center;
  margin-top: -10px;
}
#map-wrapper .sidecontrol .button-wrapper{
  margin: auto;
  text-align: center;    
}

.prev-example a, .next-example a{
  color: #222;
  display: block;
  padding: 20px;
  width: 45%; 
  background-color: #80bf43;
  margin-bottom: 50px;  
  text-decoration: none;    
}

.prev-example a{
  float: left;
}
.next-example a{
  float: right;
  text-align: right;
}

.prev-example a:before{
font-family: "Glyphicons Halflings";
content: "\E069";
color: #FFF;
margin-right: 10px;
}

.next-example a:after{
font-family: "Glyphicons Halflings";
content: "\E077";
color: #FFF;
margin-left: 10px;
}

.prev-example a:hover, .next-example a:hover{
  text-decoration: none;
  background-color: #bae568;
  color: #444;
}

.clearfix{
  clear: both;
}

.animation-progress-bar{
  position: absolute;
  bottom: 0;
  left: 0;
  height: 5px;
  width: 0;
  background-color: #fbd448;
  z-index: 10000;
}

.chart, .chart-wrapper{
  width: 100%;
  margin-bottom: 20px;
}

.legend-wrapper {    
  margin-bottom: 45px !important;
}

.legend-wrapper .legend-div {    
  margin-left: 10px;
  background-color:#D8D8D8;
  background-color:rgba(255,255,255,.8);border-radius:5px;
  padding: 5px;
  color:black;    
}

.information{
  margin: 0 20px;
  color: #333;
}

.information a{
  color: #80bf43;
}

.disclaimer{
  margin: 20px 0;
  color: #333;
  background-color: #f0f0f0;
  font-size: 0.75em;
  padding: 5px;
}


.controls {
    display: flex;
  }
  
  .radio {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
